<template>
  <div class="payment-view">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <div class="payment-context"><i class="payment-shop"></i>
      <div class="payment-shop-info">
        <p>￥{{money}}</p><span>{{shopName}}</span></div>
    </div>
    <section class="payment-type">
      <header class="payment-title">支付方式</header>
      <div class="payment-type-item"><i class="zhifubao-icon"></i><span>支付宝支付</span><i class="iconfont payment-select" :class="{'icon-selected':payType=='0'}" @click="selected('0')"></i></div>
      <div class="payment-type-item no-bottom"><i class="wechat-icon"></i><span>微信支付</span><i class="iconfont payment-select" :class="{'icon-selected':payType=='1'}" @click="selected('1')"></i></div>
    </section>
    <footer class="setting-psw-btn" @click="topay">确认支付 ￥{{money}}</footer>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '支付订单',
        routerName: 'check',
        fixed: false,
        agreementText: '',
        rightText: '',
        money: 0,
        shopName: '',
        payType: ''
      }
    },
    created: function () {
      var _this = this
      if (this.$route.params.money) {
        _this.money = this.$route.params.money
        _this.shopName = this.$route.params.shopName
        localStorage.money = this.$route.params.money
        localStorage.shopName = this.$route.params.shopName
      } else {
        _this.money = localStorage.money
        _this.shopName = localStorage.shopName
      }
    },
    methods: {
      selected: function (val) {
        this.payType = val
      },
      topay: function () {
        this.$router.push({
          name: 'paySuccess'
        })
      }
    }
  }
</script>

<style>
/* 支付页面 */
.payment-view{
  background-color: #F5F5F5;
  height: 100%;
}
.payment-context,.check-context{
  padding: .20rem .15rem;
  background-color: white;
  height: .80rem;
  display: block;
}
.payment-type{
  width: 100%;
  display: block;
  margin-top: .05rem;
  background-color: white;
}
.payment-title{
  position: relative;
  font-size: .14rem;
    color: #999999;
    line-height: .35rem;
}
.payment-shop-info{
  display: inline-block;
  vertical-align: top;
  padding: .03rem .15rem;
}
.payment-shop-info>p{
  /*font-size: .14rem;*/
  font-weight: 500;
  color: #ff8b20;
}
.payment-shop-info>span{
  /*font-size: .14rem;*/
  font-weight: 500;
  color: black;
}
.payment-title:before{
  position: absolute;
  content: "";
  left: -.15rem;
  right: 0;
  bottom: 0;
  border-bottom: .01rem solid #E5E5E5;
}
.payment-shop{
  width: .42rem;
  height: .42rem;
  display: inline-block;
  background: url(/static/image/pay/pay_icon_store@3x.png) no-repeat;
  background-size: 100% 100%;
}
.payment-type{
  padding-left: .15rem;
  background-color: white;
}
.payment-type-item:before{
  position: absolute;
  content: "";
  left: 0rem;
  right: 0;
  bottom: 0;
  border-bottom: .01rem solid #E5E5E5;
}
.no-bottom:before{
  border-bottom: none;
}
.payment-type-item{
  position: relative;
  /*padding-left: .15rem;*/
  padding: .10rem .20rem .10em 0rem;
}
.zhifubao-icon{
  width: .35rem;
  height: .35rem;
  display: inline-block;
  background: url(/static/image/pay/pay_list_alipay@3x.png) no-repeat;
  background-size: 100% 100%;
}
.payment-type-item span{
    position: absolute;
    top: 50%;
    color: black;
    transform: translate(20%,-45%);
}
.payment-select{
  float: right;
  width: .20rem;
  height: .20rem;
  margin-top: 0.10rem;
  border: .01rem solid #c5c5c5;
  border-radius:.15rem;
  text-align: center;
}
.icon-selected{
  color: white;
  border:none;
  background-color: #ff9b24;
}
.wechat-icon{
  width: .35rem;
  height: .35rem;
  display: inline-block;
  background: url(/static/image/pay/pay_list_wechat@3x.png) no-repeat;
  background-size: 100% 100%;
}

</style>


